'use client';

import React, { useState, useEffect } from 'react';
import { ArrowRight, Phone } from 'lucide-react';
import Modal from './Modal';

interface FormData {
  name: string;
  email: string;
  cpf: string;
  phone: string;
}

export default function PaymentInfinityPay({ quiet = false }: { quiet?: boolean }) {
  const [data, setData] = useState<FormData>({ name: '', email: '', cpf: '', phone: '' });
  const [agree, setAgree] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const redirectToCheckout = () => {
    const checkout = process.env.NEXT_PUBLIC_INFINITYPAY_CHECKOUT_URL;
    if (!checkout) {
      alert('Configure a variÃ¡vel NEXT_PUBLIC_INFINITYPAY_CHECKOUT_URL para habilitar o checkout do Infinity Pay.');
      return;
    }
    const params = new URLSearchParams({
      name: data.name,
      email: data.email,
      cpf: data.cpf,
      phone: data.phone,
      amount: '14999', // em centavos
      description: 'Plano de Treino Ricardo Team',
    });
    window.location.href = `${checkout}?${params.toString()}`;
  };

  const openWhatsApp = () => {
    const msg = 'OlÃ¡ Ricardo! Acabei de realizar o pagamento do plano e estou enviando o comprovante.';
    const url = `https://wa.me/5511999999999?text=${encodeURIComponent(msg)}`;
    window.open(url, '_blank');
  };

  const canSubmit = () => {
    return data.name && data.email && data.cpf && data.phone && agree;
  };

  // FunÃ§Ã£o global para abrir o modal a partir de outros componentes + fallback
  useEffect(() => {
    (window as any).openSignupModal = () => setIsModalOpen(true);

    const checkOpenFromHash = () => {
      try {
        if (window.location.hash === '#open-signup') {
          setIsModalOpen(true);
          if (window.history) {
            window.history.replaceState(null, '', window.location.pathname + window.location.search);
          }
        }
      } catch {}
    };

    // Fallback: abre automaticamente se houver flag persistida ou hash na URL
    try {
      const shouldOpen = localStorage.getItem('openSignupOnLoad') === '1' || window.location.hash === '#open-signup';
      if (shouldOpen) {
        setIsModalOpen(true);
        localStorage.removeItem('openSignupOnLoad');
        if (window.history) {
          window.history.replaceState(null, '', window.location.pathname + window.location.search);
        }
      }
    } catch {}

    window.addEventListener('hashchange', checkOpenFromHash);
    return () => {
      delete (window as any).openSignupModal;
      window.removeEventListener('hashchange', checkOpenFromHash);
    };
  }, []);

  return (
    <>
      {!quiet && (
        <section id="pagamento" className="py-20 bg-gray-50">
          <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="text-center mb-12">
              <h2 className="text-4xl md:text-5xl font-bold text-gray-900 mb-6 font-orbitron">
                Finalizar <span className="text-orange-600">Pagamento</span>
              </h2>
              <p className="text-xl text-gray-600 max-w-2xl mx-auto">
                Clique em contratar para abrir o formulÃ¡rio seguro e concluir o pagamento de R$ 149,99
              </p>
            </div>

        <div className="card-modern p-10 relative overflow-hidden">
          {/* Card decoration */}
          <div className="absolute top-0 right-0 w-24 h-24 gradient-bg-primary opacity-10 rounded-full -translate-y-12 translate-x-12"></div>
          
          {/* CTA abre o modal */}
          <div className="text-center space-y-6">
            <div className="gradient-bg-primary p-6 rounded-2xl text-white">
              <div className="flex items-center justify-center gap-4 mb-2">
                <span className="text-2xl">ðŸ’°</span>
                <span className="text-lg font-semibold">Valor Total:</span>
                <span className="text-3xl font-bold text-yellow-300">R$ 149,99</span>
              </div>
              <p className="text-white/90 text-sm">Pagamento Ãºnico â€¢ Sem mensalidades â€¢ Acesso completo</p>
            </div>

            <button
              onClick={() => setIsModalOpen(true)}
              className="w-full btn-primary text-xl py-5 shadow-2xl hover:shadow-3xl transform hover:scale-105 transition-all duration-300 flex items-center justify-center gap-2"
            >
              ðŸš€ Contratar Agora
              <ArrowRight className="w-5 h-5" />
            </button>

            <div className="flex items-center justify-center gap-4 text-sm text-gray-500">
              <span>ðŸ”’ Pagamento Seguro</span>
              <span>â€¢</span>
              <span>âœ… Dados Protegidos</span>
              <span>â€¢</span>
              <span>âš¡ Processamento RÃ¡pido</span>
            </div>
          </div>
        </div>

          {/* ApÃ³s pagamento */}
          <div className="mt-10 card-modern p-8 text-center">
            <h4 className="text-2xl font-bold text-gray-900 mb-4">ðŸ“± ApÃ³s o Pagamento</h4>
            <p className="text-gray-700 mb-6 text-lg">
              Envie o comprovante via WhatsApp para o Ricardo. Seu treino personalizado comeÃ§arÃ¡ imediatamente apÃ³s a confirmaÃ§Ã£o!
            </p>
            <button onClick={openWhatsApp} className="btn-outline flex items-center gap-3 mx-auto text-lg px-8 py-4">
              <Phone className="w-5 h-5" />
              Abrir WhatsApp do Ricardo
            </button>
            <p className="text-gray-500 text-sm mt-4">
              Resposta em atÃ© 2 horas â€¢ Suporte completo incluÃ­do
            </p>
          </div>
        </div>
        </section>
      )}

      {/* Modal com formulÃ¡rio */}
      <Modal open={isModalOpen} onClose={() => setIsModalOpen(false)} title="Cadastro e Pagamento">
        <form onSubmit={(e) => { e.preventDefault(); redirectToCheckout(); }} className="space-y-6">
          <div className="grid md:grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-3">ðŸ‘¤ Nome Completo</label>
              <input
                name="name"
                value={data.name}
                onChange={onChange}
                className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all duration-300"
                placeholder="Seu nome completo"
              />
            </div>
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-3">ðŸ“§ E-mail</label>
              <input
                name="email"
                type="email"
                value={data.email}
                onChange={onChange}
                className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all duration-300"
                placeholder="seu@email.com"
              />
            </div>
          </div>

          <div className="grid md:grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-3">ðŸ†” CPF</label>
              <input
                name="cpf"
                value={data.cpf}
                onChange={onChange}
                className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all duration-300"
                placeholder="000.000.000-00"
              />
            </div>
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-3">ðŸ“± Telefone/WhatsApp</label>
              <input
                name="phone"
                value={data.phone}
                onChange={onChange}
                className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all duration-300"
                placeholder="(11) 99999-9999"
              />
            </div>
          </div>

          <label className="flex items-center gap-3 text-gray-700 bg-gray-50 p-4 rounded-xl">
            <input type="checkbox" checked={agree} onChange={(e) => setAgree(e.target.checked)} className="w-5 h-5" />
            <span className="text-sm">Concordo em enviar o comprovante de pagamento via WhatsApp apÃ³s concluir.</span>
          </label>

          <button
            type="submit"
            disabled={!canSubmit()}
            className="w-full btn-primary text-xl py-5 shadow-2xl hover:shadow-3xl transform hover:scale-105 transition-all duration-300 flex items-center justify-center gap-2"
          >
            ðŸ’³ Pagar R$ 149,99 - Infinity Pay
            <ArrowRight className="w-5 h-5" />
          </button>
        </form>
      </Modal>
    </>
  );
}