'use client';

import React, { useEffect } from 'react';

interface ModalProps {
  open: boolean;
  onClose: () => void;
  title?: string;
  children: React.ReactNode;
  variant?: 'square' | 'default';
}

export default function Modal({ open, onClose, title, children, variant = 'square' }: ModalProps) {
  useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if (e.key === 'Escape') onClose();
    };
    if (open) {
      document.addEventListener('keydown', onKey);
      document.body.style.overflow = 'hidden';
    }
    return () => {
      document.removeEventListener('keydown', onKey);
      document.body.style.overflow = '';
    };
  }, [open, onClose]);

  if (!open) return null;

  const containerClass =
    variant === 'square'
      ? 'w-full max-w-xl aspect-square card-modern overflow-hidden relative ring-1 ring-black/5'
      : 'w-full max-w-2xl card-modern p-8 relative';

  return (
    <div className="fixed inset-0 z-50">
      <div className="absolute inset-0 bg-black/60 backdrop-blur-sm" onClick={onClose} />

      <div className="absolute inset-0 flex items-center justify-center p-4">
        <div className={containerClass}>
          {variant === 'square' ? (
            <div className="absolute top-0 left-0 right-0 h-14 gradient-bg-primary text-white flex items-center justify-between px-6">
              <div className="font-orbitron text-lg font-bold">{title}</div>
              <button
                onClick={onClose}
                className="text-white/80 hover:text-white transition-colors"
                aria-label="Fechar"
              >
                âœ•
              </button>
            </div>
          ) : (
            <button
              onClick={onClose}
              className="absolute top-4 right-4 text-gray-400 hover:text-gray-600 transition-colors"
              aria-label="Fechar"
            >
              âœ•
            </button>
          )}

          {variant === 'square' ? (
            <div className="pt-16 p-6 md:p-8 h-full overflow-y-auto">
              {children}
            </div>
          ) : (
            <>
              {title && (
                <h3 className="text-2xl font-bold text-gray-900 mb-6 font-orbitron">{title}</h3>
              )}
              <div>{children}</div>
            </>
          )}
        </div>
      </div>
    </div>
  );
}